import app from '../app';
import Component from '../../common/Component';
import ItemList from '../../common/utils/ItemList';
import LoadingIndicator from '../../common/components/LoadingIndicator';
import type Mithril from 'mithril';

export interface IWelcomeHeroAttrs { }

export default class RightPanel extends Component<IWelcomeHeroAttrs> {
  private tags: any[] = [];
  private footerHtml: any = '';
  private loading: boolean = false;
  private error: string | null = null;
  private hotList: any[] = [];
  private showTagName: string = '';
  private activeTagId: string | null = null;

  oninit(vnode: Mithril.Vnode<IWelcomeHeroAttrs, this>) {
    super.oninit(vnode);
    this.loadTags();
  }

  async loadTags() {
    try {
      this.loading = true;
      this.error = null;
      m.redraw();
      const allResources = app.data.resources;
      // this.tags = app.store.all('tags');
      this.tags = allResources.filter((item) => item.type === 'tags' && item.attributes.name !== '资源求助' && item.attributes.name !== '其他').sort((a, b) => {
        if (a.attributes.position === null) return 1;
        if (b.attributes.position === null) return -1;
        return a.attributes.position - b.attributes.position;
      });
      // if (this.tags.length === 0) {
      //   this.tags = await app.store.find('tags');
      // }
      this.showTagName = this.tags[0].attributes.slug;
      // 确保论坛数据已加载
      if (!app.forum.attribute('footerHtml')) {
        await app.store.find('forum', app.forum.id());
      }
      // 默认选中第一个标签
      if (this.tags.length > 0 && !this.activeTagId) {
        this.activeTagId = this.tags[0].id;
      }
      this.getHotList(this.showTagName);
      // 获取 footerHtml 内容
      this.footerHtml = app.forum.attribute('footerHtml');
    } catch (e) {
      this.error = '加载标签失败';
      console.error('Failed to load tags:', e);
    } finally {
      this.loading = false;
      m.redraw();
    }
  }

  // 获取当前tag里的热门主题
  async getHotList(type: string) {
    try {
      const res = await app.request({
        method: 'get',
        url: `${app.forum.attribute('baseUrl')}/api/discussions`,
        params: {
          include: 'user,lastPostedUser,tags,tags.parent,firstPost',
          'filter[tag]': type,
          sort: '-commentCount',
          'page[offset]': 0,
        },
      });
      this.hotList = res.data;
      m.redraw();
    } catch (error) {
      console.log('获取热门资源列表失败', error);
    }
  }

  // 新增：处理标签点击事件
  async handleTagClick(tag: any, event: Event) {
    event.preventDefault(); // 阻止默认跳转行为
    this.activeTagId = tag.id;
    const updateName = tag.attributes.slug;
    await this.getHotList(updateName);
    m.redraw();
  }

  linkClick(hot: any, e: Event) {
    window.open(location.origin + '/d/' + hot.attributes.slug, '_blank');
    // location.href = location.origin + '/d/' + hot.attributes.slug
  }

  view(vnode: Mithril.Vnode<IWelcomeHeroAttrs, this>) {
    return (
      <div className="right_panel">
        <div className="right_title_list">
          <div className="right_title">热门资源</div>
          {this.loading ? <LoadingIndicator /> : this.error ? <div className="error">{this.error}</div> : this.rightbarItems().toArray()}
        </div>
        {/* 在后台底部自定义文件进行配置 */}
        {m.trust(this.footerHtml)}
      </div>
    );
  }

  swiperInit() {
    const mySwiper = new Swiper('.right-swiper-container', {
      direction: 'horizontal', // 水平方向
      loop: true, // 循环模式
      autoplay: {
        delay: 3000,
        disableOnInteraction: false, // 用户操作后继续自动播放
      },
      speed: 500, // 切换速度500ms
      grabCursor: true, // 拖拽时显示抓手光标
      effect: "coverflow",
      slidesPerView: 'auto', // 自动计算可见数量
      centeredSlides: true, // 活动幻灯片居中显示
      observer: true, // Swiper观察器
      observeParents: true, // Swiper的父级元素发生变化时自动更新
      coverflowEffect: {
        rotate: 0, // 旋转角度
        stretch: -36,
        depth: 200, // 侧面图片景深
        slideShadows: false
      },
      // 分页器
      pagination: {
        el: '.swiper-pagination-custom',
        clickable: true,
        bulletClass: 'swiper-pagination-bullet-custom',
        bulletActiveClass: 'swiper-pagination-bullet-custom-active',
        renderBullet: function (index, className) {
          return '<span class="' + className + '"></span>';
        },
      },
      navigation: {
        nextEl: '.swiper-button-next-custom',
        prevEl: '.swiper-button-prev-custom',
        hiddenClass: 'swiper-button-hidden',
      },
      on: {
        init: function () {
          // console.log('Swiper初始化完成');
        },
        slideChange: function () {
          // console.log("当前活动索引:", this.realIndex)
        },
      },
    });
  }

  dragList() {
    const container = document.getElementById('dragContainer');
    const content = document.getElementById('dragContent');
    const scrollToLeftBtn = document.getElementById('scrollToLeftBtn');
    const scrollToRightBtn = document.getElementById('scrollToRightBtn');
    const tagItems = document.querySelectorAll('.tag_item');

    let isDragging = false;
    let startX;
    let startTransform = 0;

    // 更新按钮状态
    function updateButtonStates() {
      const currentTransform = getCurrentTransform();
      const maxScroll = content.scrollWidth - container.clientWidth;

      // scrollToLeftBtn.classList.toggle('hidden', currentTransform >= 0);
      // scrollToRightBtn.classList.toggle('hidden', currentTransform <= -maxScroll || maxScroll <= 0);
    }

    // 滚动到指定位置
    function scrollToPosition(targetPosition) {
      const maxScroll = content.scrollWidth - container.clientWidth;
      const clampedPosition = Math.min(0, Math.max(-maxScroll, targetPosition));

      content.style.transform = `translateX(${clampedPosition}px)`;
      updateButtonStates();
      if (clampedPosition === -maxScroll) {
        scrollToRightBtn.classList.add('hidden');
        scrollToLeftBtn.classList.remove('hidden');
      } else if (clampedPosition === 0) {
        scrollToRightBtn.classList.remove('hidden');
        scrollToLeftBtn.classList.add('hidden');
      } else {
        scrollToRightBtn.classList.remove('hidden');
        scrollToLeftBtn.classList.remove('hidden');
      }
    }

    // 点击标签使其居中
    function centerTagOnClick(tag) {
      const containerWidth = container.clientWidth;
      const tagRect = tag.getBoundingClientRect();
      const containerRect = container.getBoundingClientRect();

      // 计算标签相对于容器的位置
      const tagCenter = tagRect.left + tagRect.width / 2 - containerRect.left;
      const desiredCenter = containerWidth / 2;

      // 计算需要的位移
      const currentTransform = getCurrentTransform();
      const newTransform = currentTransform + (desiredCenter - tagCenter);

      scrollToPosition(newTransform);
    }

    // 为每个标签添加点击事件
    tagItems.forEach((tag) => {
      tag.addEventListener('click', (e) => {
        // 如果正在拖动，则不触发点击居中
        if (isDragging) return;

        centerTagOnClick(tag);

        // 更新active状态
        tagItems.forEach((item) => item.classList.remove('active'));
        tag.classList.add('active');
      });
    });

    // 左右按钮事件保持不变
    scrollToLeftBtn.addEventListener('click', () => {
      const update = Math.min(0, getCurrentTransform() + 192);
      scrollToPosition(update);
    });

    scrollToRightBtn.addEventListener('click', () => {
      const update = Math.max(-(content.scrollWidth - container.clientWidth), getCurrentTransform() - 192);
      scrollToPosition(update);
    });

    // 拖动相关事件保持不变
    // content.addEventListener('mousedown', (e) => {
    //   isDragging = true;
    //   startX = e.clientX;
    //   startTransform = getCurrentTransform();
    //   content.style.cursor = 'grabbing';
    //   content.style.transition = 'none';
    //   e.preventDefault();
    // });

    // document.addEventListener('mousemove', (e) => {
    //   if (!isDragging) return;
    //   const dx = e.clientX - startX;
    //   scrollToPosition(startTransform + dx);
    // });

    // document.addEventListener('mouseup', () => {
    //   isDragging = false;
    //   content.style.cursor = 'grab';
    //   content.style.transition = 'transform 0.3s ease';
    // });

    function getCurrentTransform() {
      const style = window.getComputedStyle(content);
      const matrix = new DOMMatrix(style.transform);
      return matrix.m41;
    }

    // 初始化
    updateButtonStates();
  }

  getRandomFloat(min, max) {
    const randomNum = Math.random() * (max - min) + min;
    return randomNum.toFixed(1);
  }

  oncreate(vnode) {
    super.oncreate(vnode);
    this.dragList();
    this.swiperInit();
  }

  rightbarItems() {
    const items = new ItemList<Mithril.Children>();
    if (this.tags.length > 0) {
      items.add(
        'tagsContent',
        <div class="scroll-container">
          <div class="scroll-btn hidden left" id="scrollToLeftBtn">
            <svg xmlns="http://www.w3.org/2000/svg" width="5" height="8" viewBox="0 0 5 8" fill="none">
              <path d="M4.16366 3.72501C4.30287 3.86423 4.31447 4.08273 4.19846 4.23516L4.16366 4.27499L1.05255 7.3861C0.900676 7.53797 0.654445 7.53797 0.502575 7.3861C0.36336 7.24688 0.351759 7.02838 0.467771 6.87595L0.502575 6.83613L3.33889 3.99985L0.502575 1.16387C0.36336 1.02466 0.351759 0.806157 0.467771 0.653727L0.502575 0.613903C0.641789 0.474688 0.860293 0.463087 1.01272 0.579099L1.05255 0.613903L4.16366 3.72501Z" fill="#657083" />
            </svg>
          </div>
          <div className="popular-tags" id="dragContainer">
            <ul class="tag_list" id="dragContent">
              {this.tags.map((tag) => (
                <li key={tag.id} class={`tag_item ${this.activeTagId === tag.id ? 'active' : ''}`}>
                  <span onclick={(e: Event) => this.handleTagClick(tag, e)}>{tag.attributes.name}</span>
                </li>
              ))}
            </ul>
          </div>
          <div class="scroll-btn" id="scrollToRightBtn">
            <svg xmlns="http://www.w3.org/2000/svg" width="5" height="8" viewBox="0 0 5 8" fill="none">
              <path d="M4.16366 3.72501C4.30287 3.86423 4.31447 4.08273 4.19846 4.23516L4.16366 4.27499L1.05255 7.3861C0.900676 7.53797 0.654445 7.53797 0.502575 7.3861C0.36336 7.24688 0.351759 7.02838 0.467771 6.87595L0.502575 6.83613L3.33889 3.99985L0.502575 1.16387C0.36336 1.02466 0.351759 0.806157 0.467771 0.653727L0.502575 0.613903C0.641789 0.474688 0.860293 0.463087 1.01272 0.579099L1.05255 0.613903L4.16366 3.72501Z" fill="#657083" />
            </svg>
          </div>
        </div>,
        20
      );
      items.add(
        'hotList',
        <div className="hot_wrap">
          <ul class="hot_list">
            {this.hotList.slice(0, 10).map((hot, index) => (
              <li key={hot.id} class={`hot_item`}>
                <div class="hot_left">
                  {index === 0 ? (
                    <div class="hot_count">
                      <img src="https://pic1.imgdb.cn/item/68ba444458cb8da5c87f12c1.png" alt="图标" />
                    </div>
                  ) : index === 1 ? (
                    <div class="hot_count">
                      <img src="https://pic1.imgdb.cn/item/68ba444458cb8da5c87f12c4.png" alt="图标" />
                    </div>
                  ) : index === 2 ? (
                    <div class="hot_count">
                      <img src="https://pic1.imgdb.cn/item/68ba444458cb8da5c87f12c5.png" alt="图标" />
                    </div>
                  ) : (
                    <div class="hot_count">{index === 9 ? '10' : (index + 1)}</div>
                  )}
                  <div class="title" title={hot.attributes.title} onclick={(e: Event) => this.linkClick(hot, e)}>
                    {hot.attributes.title}
                  </div>
                </div>
                {/* <div class={`hot_num ${index > 2 ? 'hide' : ''}`}>
                  <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14" fill="none">
                    <path d="M11.5608 8.18358C11.5608 7.60258 11.244 6.31808 10.5563 5.01958C10.544 5.45358 10.1923 6.50533 9.93328 6.78533C9.76003 6.29533 9.71628 3.02458 5.40253 1.92383C5.66503 5.17708 2.42578 5.12983 2.42578 8.64033C2.42578 10.5058 3.54403 12.1106 5.14528 12.8228C4.77953 10.6703 6.90578 10.3938 6.56278 8.17658C8.80278 9.51708 9.32953 11.0851 8.72403 12.8718C10.5125 12.1228 11.7043 10.2486 11.5608 8.18358Z" fill="#BFC9D5"/>
                  </svg>
                  { 
                    index === 0 ? (
                      <span>{ this.getRandomFloat(1, 2) + 'w'}</span>
                    ) : index === 1 ? (
                      <span>{ this.getRandomFloat(0.5, 1) + 'w'}</span>
                    ) : (
                      <span>{ this.getRandomFloat(0.1, 0.5) + 'w'}</span>
                    )
                  }
                </div> */}
              </li>
            ))}
          </ul>
        </div>,
        20
      );
    }
    return items;
  }
}
