                                    <path d="M8.00065 1.3335C11.6826 1.3335 14.6673 4.31826 14.6673 8.00016C14.6673 11.6821 11.6826 14.6668 8.00065 14.6668C6.19585 14.6668 4.55855 13.9497 3.35818 12.7847C2.10976 11.5732 1.33398 9.87726 1.33398 8.00016C1.33398 6.21103 2.03876 4.5865 3.18584 3.38906" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" />
