import app from '../app';
import Component from '../../common/Component';
import Button from '../../common/components/Button';
import type Mithril from 'mithril';
import ItemList from '../../common/utils/ItemList';
import Search from '../components/Search';

export interface IWelcomeHeroAttrs { }

const LOCAL_STORAGE_KEY = 'welcomeHidden';

/**
 * The `WelcomeHero` component displays a hero that welcomes the user to the
 * forum.
 */
export default class WelcomeHero extends Component<IWelcomeHeroAttrs> {
  /**
   * @deprecated Extend the `isHidden` method instead.
   */
  hidden: boolean = false;
  private headerHtml: any = '';

  oninit(vnode: Mithril.Vnode<IWelcomeHeroAttrs, this>) {
    super.oninit(vnode);
    this.headerHtml = app.forum.attribute('headerHtml');
  }

  view(vnode: Mithril.Vnode<IWelcomeHeroAttrs, this>) {
    if (this.isHidden()) return null;

    return (
      <header className="Hero WelcomeHero">
        <div class='Hero_left'>
          <img src="https://pic1.imgdb.cn/item/68ba484c58cb8da5c87f1d69.png" alt="" />
          <Search state={app.search} />
          {/* 配置跳转文案 */}
          <div class="search_list">
            {m.trust(this.headerHtml)}
          </div>
        </div>
        {/* 配置轮播图 */}
        <div className="container">{this.viewItems().toArray()}</div>
        <nav class="nav_title">
          <ul class="nav_list">
            <li class="nav_item" onclick={(e: Event) => {
              // m.route.set('/posterwall', { from: '热门影视' });
              window.open('/posterwall?from=' + encodeURIComponent('热门影视'), '_blank');
            }}>
              <img class='nav_item_img' src="https://pic1.imgdb.cn/item/68ba485c58cb8da5c87f1d78.png" alt="热门影视" />
              <h3>热门影视</h3>
              <p>最新、最热，一网打尽</p>
            </li>
            <li class="nav_item" onclick={(e: Event) => { window.open('/posterwall?from=' + encodeURIComponent('4K动漫'), '_blank'); }}>
              <img class='nav_item_img' src="https://pic1.imgdb.cn/item/68ba485c58cb8da5c87f1d7a.png" alt="4K动漫" />
              <h3>4K动漫</h3>
              <p>实时更新，画质拉满</p>
            </li>
            <li class="nav_item" onclick={(e: Event) => { window.open('/posterwall?from=' + encodeURIComponent('爆火短剧'), '_blank'); }}>
              <img class='nav_item_img' src="https://pic1.imgdb.cn/item/68ba485c58cb8da5c87f1d77.png" alt="爆火短剧" />
              <h3>爆火短剧</h3>
              <p>古装穿越，逆袭甜宠</p>
            </li>
            <li class="nav_item" onclick={(e: Event) => { window.open('/posterwall?from=' + encodeURIComponent('游戏畅玩'), '_blank'); }}>
              <img class='nav_item_img' src="https://pic1.imgdb.cn/item/68ba485c58cb8da5c87f1d76.png" alt="游戏畅玩" />
              <h3>游戏畅玩</h3>
              <p>全网游戏，免费畅玩</p>
            </li>
            <li class="nav_item" onclick={(e: Event) => { window.open('/posterwall?from=' + encodeURIComponent('欧美日韩'), '_blank'); }}>
              <img class='nav_item_img' src="https://pic1.imgdb.cn/item/68ba485c58cb8da5c87f1d79.png" alt="欧美日韩" />
              <h3>欧美日韩</h3>
              <p>爆款热片，畅快观看</p>
            </li>
          </ul>
        </nav>
      </header>
    );
  }

  /**
   * Hide the welcome hero.
   */
  hide() {
    localStorage.setItem(LOCAL_STORAGE_KEY, 'true');
  }

  /**
   * Determines whether the welcome hero should be hidden.
   *
   * @returns if the welcome hero is hidden.
   */
  isHidden(): boolean {
    if (!app.forum.attribute<string>('welcomeTitle')?.trim()) return true;
    if (localStorage.getItem(LOCAL_STORAGE_KEY)) return true;
    if (this.hidden) return true;

    return false;
  }

  viewItems(): ItemList<Mithril.Children> {
    const items = new ItemList<Mithril.Children>();

    const slideUp = () => {
      this.$().slideUp(this.hide.bind(this));
    };

    items.add(
      'dismiss-button',
      <Button
        icon="fas fa-times"
        onclick={slideUp}
        className="Hero-close Button Button--icon Button--link"
        aria-label={app.translator.trans('core.forum.welcome_hero.hide')}
      />,
      100
    );

    items.add('content', <div className="containerNarrow">{this.contentItems().toArray()}</div>, 80);

    return items;
  }

  contentItems(): ItemList<Mithril.Children> {
    const items = new ItemList<Mithril.Children>();

    // items.add('title', <h1 className="Hero-title">{app.forum.attribute('welcomeTitle')}</h1>, 100);

    items.add('subtitle', <div className="Hero-subtitle">{m.trust(app.forum.attribute('welcomeMessage'))}</div>);

    return items;
  }
}
